      var obj = {}
      this.personaArray.flat().map((persona, index) => {
        if (!obj[persona.title]) { obj[persona.title] = 0 }
        obj[persona.title] += persona.weight
      })
      return obj
    },
    characterScore () {
      var obj = {}
      this.characterArray.flat().map((character, index) => {
        if (!obj[character.title]) { obj[character.title] = 0 }
        obj[character.title] += character.weight
      })
      return obj
    }
