        var question_obj = {}
        var answers = []
        for (var j = 0; j < question.length; j++) {
          if (j === 0) {
            question_obj.title = question[j]
          }
          if (j === 1) {
            question_obj.multiple = question[j]
          }
          if (j > 1) {
            var answer_array = question[j]
            var answer_obj = {}
            for (var k = 0; k < answer_array.length; k++) {
